import { useStaticQuery, graphql, Link } from "gatsby";
import React, { useState } from "react";
import Category from "./Category";
import Profile from "./Profile";

const SideBar = (props:any) => {    
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(
                sort: {frontmatter: {date: ASC}}
            ) {
                edges {
                    node {
                        frontmatter {
                            title
                            date(formatString: "YY-MM-DD")
                            slug
                            category
                        }
                        id
                        excerpt(format: MARKDOWN)
                    }
                }
            }
            category: allMarkdownRemark(limit: 2000) {
                group(field: { frontmatter: { category: SELECT } }) {
                    fieldValue
                    totalCount
                }
            }
        }`);

    const [category, setCategory] = useState("");    
    const selectCategory = (category: string) => {
        setCategory(category);
    };


    const categories = data.category.group;
    const list = data.allMarkdownRemark.edges
                        .filter((d:any) => d.node.frontmatter?.category == (category == "" ? props?.category: category));
    return (
        <div className="my-3">
            <div className="mx-5">
                <Profile></Profile>
                <Category categories={categories} selectCategory={selectCategory} ></Category>
                <div className="mx-3 px-3 py-2 text-xs font-semibold text-white bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500">POSTS</div>
                {list?.map(({ node }: any) => (
                    <div className="my-3 mx-6" key={node.id}>
                        <div className="text-sm my-1 hover:text-rose-300">
                            <Link to={node.frontmatter.slug}>
                                <span className="font-semibold px-2">{node.frontmatter.title}</span>
                                <span className="mx-2 italic text-xs text-gray-300">{node.frontmatter.date}</span>
                            </Link>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )    
};

export default SideBar;


